import { getDetailWork } from "@/libs/api";
import parse from "html-react-parser";
import styles from "./detailWork.module.css";
import Image from "next/image";
const { articleArea, imageArea, bodyArea, urlP } = styles;

type Props = {
  params: Promise<{ contentId: string }>;
};

export default async function DetailWork({ params }: Props) {
  const { contentId } = await params;
  const { data } = await getDetailWork(contentId);
  const { title, thumbnail, url, github, body, run, categories } = data;
  const reactElement = parse(body);

  return (
    <section>
      <article className={articleArea}>
        <h1>{title}</h1>
        <ul>
          {categories.map((cat) => (
            <li key={cat}>{cat}</li>
          ))}
        </ul>
        <div className={imageArea}>
          <Image
            src={thumbnail.url}
            alt={title}
            width={thumbnail.width}
            height={thumbnail.height}
          />
          <span>{run}</span>
        </div>
        <p className={urlP}>
          <span>url:</span>
          <a href={url}>{url}</a>
        </p>
        <p className={urlP}>
          <span>gitHub:</span>
          {github}
        </p>

        <div className={bodyArea}> {reactElement}</div>
      </article>
    </section>
  );
}
